button {
  box-shadow: 0 0 5px cyan, 0 0 25px cyan;
}
button:hover {
  box-shadow: 0 0 5px cyan, 0 0 25px #00ffff, 0 0 50px cyan, 0 0 100px cyan,
    0 0 200px cyan;
} 
:root {
  --glow-color: hsl (186 100% 69%);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: linear-gradient(
    to right,
    rgb(12, 158, 70),
    #7856b2,
    rgb(195, 149, 186)
  );
}
body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn {
  position: relative;
  color: var(--glow-color);
  cursor: pointer;
  padding: 0.3em 1em;
  border: 0.15em solid var(--glow-color);
  border-radius: 0.45em;
  background: 0 5px #5d3ab0;
  perspective: 1em;
  font-family: "Raleway", sans-serif;
  font-size: 1em;
  font-weight: 900;
  letter-spacing: 1em;
} 
